//Profile private

import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Image, Button, Modal, Form, Container, Row, Col } from 'react-bootstrap';
import { Link, withRouter } from 'react-router-dom';
import CommentCard from './CommentCard.jsx';
import CountdownTimer from './CountdownTimer.jsx';
import '../css/profile.css';
import pencilIcon from '../pencil_edit.svg';
import profTestData from '../test_data_prof.json';

class Profile extends Component {

  constructor(props) {
	super(props);
    this.state = {
      visShare: false,
      summary: profTestData.profile_summary,
      tempText: profTestData.profile_summary,
      personName: profTestData.profile_name,
      location: profTestData.profile_location,
      lastLogin: profTestData.last_login,
      summEdit: false,
      submittable: false
    };

    this.showShare = this.showShare.bind(this);
    this.hideShare = this.hideShare.bind(this);
    this.changeTempText = this.changeTempText.bind(this);
    this.changeSummary = this.changeSummary.bind(this);
    //this.tester = this.tester.bind(this);
  }

  showShare = () => {
    this.setState({
      visShare: true
    });
  };

  hideShare = () => {
    this.setState({
      visShare: false
    });
  };

  changeTempText = (event) => {
    this.setState({
      tempText: event.target.value
    });
  };

  changeSummary = () => {
    this.setState({
      summary: this.state.tempText,
      summEdit: false
    });
  };

  openSummary = () => {
    this.setState({
      summEdit: true
    });
  };

  /*
  tester = () => {
    console.log(profTestData.profile_comments);
  };
  */

  componentDidMount() {
    const temptime = new Date(profTestData.profile_scorer_open);
    const currtime = new Date();
    const diff = temptime.getTime() - currtime.getTime();
    if (diff > 0) {
      this.setState({
        submittable: false
      });
    }
  }

   render() {
    return(
      <Container>

        <Modal show={this.state.visShare} onHide={this.hideShare} className='share-modal'>
          <Modal.Header closeButton>
            <h2 className='kanit'>Share Your Score</h2>
          </Modal.Header>
          <Modal.Body className='overpass'>
            a link will go here eventually
          </Modal.Body>
        </Modal>

        {/* TOP SECTION */}
        <Row className='top-profile-section'>
          <Col sm={3}><Image id='profile-img' src={profTestData.profile_photo}/></Col>
          <Col>
            <h1 className='kanit' id='person-name'>{this.state.personName}</h1>
            <h2 className='kanit' id='person-location'>{this.state.location}</h2>
          </Col>
          <Col sm={2}>
            <h1 className='righteous' id='social-score'>{ profTestData.profile_total_score }</h1>
            <Button onClick={this.showShare} bsPrefix='gray-button' className='kanit'>Share</Button>
          </Col>
        </Row>

        {/* Summary section */}
        <section className='profile-section'>
          <h2 className='kanit section-header'>About You</h2>
          <div>
            <section className={'summary-filled ' + (!this.state.summEdit ? '' : 'hidden')}>
              <p className='overpass summary-text'>{this.state.summary}</p>
              <Button bsPrefix='gray-button' onClick={this.openSummary} id='edit-summary-button'><Image src={pencilIcon}/></Button>
            </section>
            <Form className={'summary-filled ' + (this.state.summEdit ? '' : 'hidden')}>
              <Form.Control as='textarea' row={3} value={this.state.tempText} onChange={this.changeTempText} className='overpass'/>
              <Button onClick={this.changeSummary} bsPrefix='gray-button' className='kanit' id='submit-summary-button'>Submit</Button>
            </Form>
          </div>
        </section>
        {/* Comment section */}
        <section className='profile-section'>
          <h2 className='kanit section-header'>Recent Comments</h2>

          <Row className='card-container'>
            {profTestData.profile_comments.map( (item) => {
               return(<CommentCard id={item.comment_id} photo={item.comment_photo} score={item.comment_score} summ={item.comment_summary} date={item.comment_date} logDate={this.state.lastLogin}/>);
            })}
            {/*<Button onClick={this.tester}>:D</Button>*/}
          </Row>
          <Button bsPrefix='gray-button' className='kanit'>See more</Button>
        </section>
        {/* Submit new score */}
        <Row className='profile-section'>
          <h2 className='kanit section-header'>Submit New Score</h2>
          <Col><Button bsPrefix='gray-button' className='kanit' id='prof-submit-button' disabled={!this.state.submittable}>Submit score</Button></Col>

          <Col className='kanit timer-section' as={Row}>
            <Col sm={6} className='timer-label'>Time remaining:</Col>
            <Col sm={6} className='timer-countdown'><CountdownTimer/></Col>
          </Col>
        </Row>
      </Container>
    );
  }

}

export default Profile;
