// Homepage
import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Link, withRouter } from 'react-router-dom';
import { Button, Modal, Form, Image, Container, Row, Col } from 'react-bootstrap';
import sslogo from '../socialscorerTM_logo_final.png';
import '../css/homepage.css';

class Homepage extends Component {

  constructor(props) {
	super(props);
    this.state = {
      visibleLogin: false,
      visibleRegister: false
    };

    this.showLogin = this.showLogin.bind(this);
    this.hideLogin = this.hideLogin.bind(this);
    this.showRegister = this.showRegister.bind(this);
    this.hideRegister = this.hideRegister.bind(this);
    this.attemptLogin = this.attemptLogin.bind(this);
  }

  showLogin = () => {
    this.setState({
      visibleLogin: true
    });
  };

  showRegister = () => {
    this.setState({
      visibleRegister: true
    });
  };

  hideLogin = () => {
    this.setState({
      visibleLogin: false
    });
  };

  attemptLogin = () => {
    //validate credentials here
    this.props.history.push('/me');
  };

  hideRegister = () => {
    this.setState({
      visibleRegister: false
    });
  };

   render() {
    return(
      <Container>
        <div className="home-title-bar">
          <Image src={sslogo} id="homepage-icon"/>
          <h1 className="righteous homepage-title"> Social Scorer </h1>
        </div>

        {/* LOGIN MODAL */}
        <Modal show={this.state.visibleLogin} onHide={this.hideLogin}>
          <Modal.Header closeButton className="righteous home-modal-title">
            Login
          </Modal.Header>
          <Modal.Body>
            <Form>
              <Form.Group as={Row} className="home-modal-formgroup">
                <Col sm={2}><Form.Label className="righteous home-modal-label">User</Form.Label></Col>
                <Col sm={10}><Form.Control type="text" placeholder="Username" className="overpass"/></Col>
              </Form.Group>
              <Form.Group as={Row} className="home-modal-formgroup">
                <Col sm={2}><Form.Label className="righteous home-modal-label">Pass</Form.Label></Col>
                <Col sm={10}><Form.Control type="password" className="overpass"/></Col>
              </Form.Group>
            </Form>
          </Modal.Body>
          <Modal.Footer className="home-modal-footer">
            <Button bsPrefix="black-button" className="righteous home-modal-submit" onClick={this.attemptLogin}>Submit</Button>
          </Modal.Footer>
        </Modal>

        {/* REGISTER MODAL */}
        {/* FIX THIS  */}
        <Modal show={this.state.visibleRegister} onHide={this.hideRegister}>
          <Modal.Header closeButton className="righteous home-modal-title">
            Register
          </Modal.Header>
          <Modal.Body>
            <Form>
              <Form.Group>
                <Form.Label className="righteous home-modal-label">Upload ID Photo</Form.Label>
                <Form.Control className="overpass" type="file" accept="image/*"/>
              </Form.Group>
            </Form>
          </Modal.Body>
          <Modal.Footer className="home-modal-footer">
            <Button bsPrefix="black-button" className="righteous home-modal-submit">Submit</Button>
          </Modal.Footer>
        </Modal>

        <div className="button-panel-homepage">
          <Button onClick={this.showLogin} className="righteous logres-button" bsPrefix="black-button">Login</Button>
          <Button onClick={this.showRegister} className="righteous logres-button" bsPrefix="black-button">Register</Button>
        </div>

        <div className="overpass homepage-desc-container">
          <p>Description of the site.</p>
        </div>
      </Container>
    );
  }

}

export default withRouter(Homepage);
