// comment cards

import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Button, Modal, Card, Image, Row, Col, Form } from 'react-bootstrap';

class CommentCard extends Component {

  constructor(props) {
    super(props);
    this.state = {
      modalVisible: false,
      commentDate: this.props.date,
      comment: this.props.summ,
      score: this.props.score,
      photo: this.props.photo,
      lastLogin: this.props.logDate,
      disputeView: false,
      disputeSent: false,
      disputeComment: '',
      modalLock: false,
      showTag: false
      //rebuttalComment: "lorem ipsum or smth idk"
    };

    this.showModal = this.showModal.bind(this);
    this.hideModal = this.hideModal.bind(this);
    this.disputeViewer = this.disputeViewer.bind(this);
    this.submitDispute = this.submitDispute.bind(this);
    this.typeDispute = this.typeDispute.bind(this);
  }

  componentDidMount() {
    const logdate = new Date(this.state.lastLogin);
    const commDate = new Date(this.state.commentDate);
    const diff = commDate.getTime() - logdate.getTime();
    if (diff > 0) {
      //console.log(diff);
      this.setState({
        showTag: true
      });
    }
  }

  showModal = () => {
    this.setState({
      modalVisible: true
    });
  };

  hideModal = () => {
    this.setState({
      modalVisible: false
    });
  };

  disputeViewer = () => {
    this.setState({
      disputeView: true
    });
  };

  submitDispute = () => {
    this.setState({
      disputeView: false,
      modalLock: true
    });
  };

  typeDispute = (event) => {
    this.setState({
      disputeComment: event.target.value
    });
  };

   render() {
    return(
      <Col sm={3}>
        <Modal show={this.state.modalVisible} onHide={this.hideModal} className='comment-modal'>
          <div className={(!this.state.disputeView ? '' : 'hidden')}>
            <Image src={this.state.photo} className='comment-modal-img'/>
            <p className='comment-img-caption overpass'>{this.state.commentDate}</p>
            <Modal.Body>
              <Row className='comment-text-content'>
                <Col sm={4}><div className='righteous card-score'>{this.state.score}</div></Col>
                <Col>
                  <h2 className='kanit'>Comment</h2>
                  <p className='overpass'>{this.state.comment}</p>
                </Col>
              </Row>

            </Modal.Body>
            <Modal.Footer><Button className='kanit' bsPrefix='gray-button' onClick={this.disputeViewer} disabled={this.state.modalLock}>{(this.state.modalLock ? 'Dispute Pending...' : 'Dispute')}</Button></Modal.Footer>
          </div>
          <div className={(this.state.disputeView ? '' : 'hidden')}>
            <h2 className='kanit'>Photo</h2>
            <Image src={this.state.photo} className='comment-modal-img'/>
            <Modal.Body>
              <h2 className='kanit'>Original Comment</h2>
              <p className='overpass'>{this.state.comment}</p>
              <h2 className='kanit'>Your Dispute</h2>
              <Form>
                <Form.Control as='textarea' rows={3} className='overpass' value={this.state.disputeComment} placeholder='Please describe your grievances' onChange={this.typeDispute}/>
              </Form>

            </Modal.Body>
            <Modal.Footer><Button className="kanit" bsPrefix="gray-button" onClick={this.submitDispute}>Submit</Button></Modal.Footer>
          </div>
        </Modal>

        <Card onClick={this.showModal} className="card-comment comment-condensed-card">
          <div className={'card-tag kanit ' + (this.state.showTag ? '' : 'hidden')}>new</div>
          <Card.Img variant="top" src={this.state.photo} className="card-comment-img"/>
          <Card.Body>
            <Card.Title className="comment-card-score righteous">{this.state.score}</Card.Title>
          </Card.Body>
        </Card>
      </Col>
    );
  }

}

export default CommentCard;
