//upload score

import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Button, Modal, Form, Container, Image, Row, Col } from 'react-bootstrap';
import { Link, withRouter } from 'react-router-dom';
import '../css/upload.css';
import facepic from '../face_screenshot.png';

class UploadPage extends Component {

  constructor(props) {
	super(props);
    this.state = {
      scoreVal: 0,
      showMultiface: false,
      showFoundface: false,
      showUpImg: false,
      disableSubmit: true,
      commentText: ''
    };

    this.changeScoreVal = this.changeScoreVal.bind(this);
    this.changeComment = this.changeComment.bind(this);
    this.findFace = this.findFace.bind(this);
    this.submitUpload = this.submitUpload.bind(this);
  }

  changeScoreVal = (event) => {
    this.setState({
      scoreVal: event.target.value
    });
  };

  changeComment = (event) => {
    this.setState({
      commentText: event.target.value
    });
    if (this.state.commentText.trim() !== '') {
      this.setState({
        disableSubmit: false
      });
    }
  };

  findFace = () => {
    this.setState({
      showFoundface: true,
      showMultiface: true,
      showUpImg: true
    });
  };

  submitUpload = () => {
    //validate credentials
    this.props.history.push('/me');
  };

   render() {
    return(
      <Container>
        <h1 className='righteous'>Score Upload</h1>

        <Form>

          <Form.Group className='upload-form-section'>
            {/*Upload image*/}
            <h2 className='kanit'>Upload image</h2>
            <Form.Control className='overpass' type='file' accept='image/*'/>
            <Button bsPrefix='gray-button' onClick={this.findFace} className='kanit'>Click to scan</Button>
            <Image id='uploaded-img' className={this.state.showUpImg ? '' : 'hidden'} src='https://previews.123rf.com/images/piksel/piksel1306/piksel130600124/20469966-business-people-cheering-at-laptop.jpg'/>
          </Form.Group>

          {/* This bar is hidden for now, it needs work still */}
          <div className="hidden">
            <Form.Group className={this.state.showMultiface ? '' : 'hidden'}>
              {/*Bar for identifying faces, make it like a flexbox or something*/}
              <p className='overpass'>Multiple faces found. Please indicate the person you want to score.</p>
              <Image className='multiface-photo' src={facepic}/>
              <Image src='#'/>
            </Form.Group>
          </div>

          <Form.Group className={'barfound ' + (this.state.showFoundface ? '' : 'hidden')}>
            {/*Section for identifying person*/}
            <h2 className='kanit'>Person Found</h2>
            <Row className='person-found-bar'>
              <Col sm={{span: 3, offset: 2}}><Image src={facepic} className='idImg'/></Col>
              <Col sm={7}><h3 className='kanit found-id-label'>Criminal Mastermind</h3></Col>
            </Row>
            <Button bsPrefix='gray-button' className='kanit'>Search Again</Button>
          </Form.Group>

          <Form.Group className='upload-form-section'>
            {/*Slider*/}
            <h2 className='kanit'>Your Score</h2>
            <Row className='justify-content-center'>
              <Col sm={6}><Form.Range min='-10' max='10' onChange={this.changeScoreVal} className='form-slider'/></Col>
              <Col sm={2}><Form.Label className='righteous slider-label'>{this.state.scoreVal}</Form.Label></Col>
            </Row>
          </Form.Group>

          <Form.Group className='upload-form-section'>
            {/*Comment*/}
            <h2 className='kanit'>Your Comment</h2>
            <Form.Control className='overpass' as='textarea' rows={3} placeholder='type comment here' value={this.state.commentText} onChange={this.changeComment}/>
          </Form.Group>
        </Form>

        <div className='button-panel-upload'>
          <Button bsPrefix='gray-button' className='righteous' as={Link} to={'/me'}>Cancel</Button>
          <Button bsPrefix='gray-button' className='righteous' disabled={this.state.disableSubmit}>Submit</Button>
        </div>

      </Container>
    );
  }

}

export default withRouter(UploadPage);
